                        <button type="button" class="btn btn-success" @click="modal(problem.id)">查看</button>
                        <button type="button" class="btn btn-warning" @click="modal(problem.id)">修改</button>
                        <button type="button" class="btn btn-danger" @click="del_video(problem)">删除</button>
                    </div>
                </td>
            </tr>
        </tbody>
        </table>


