</template>


<script>
import api from '../axios'
export default {
    data() {
        return {
            problems: []
        }
    },
    created() {
        this.init();
    },
    methods: {
        init() {
            api.show_problems().then((res) => {
                this.problems = res.data;
                console.log(this.problems);
            })
        },
        toPercent(point){
            var str=Number(point*100).toFixed(1);
            str+="%";
            return str;
        }
    }
}
</script>

