<template>
    <div class="upload">
        <div class="row">
            <div class="col-lg-2">

            </div>
            <div class="col-lg-8">
                <div class="row">
                    
                    <div class="col-lg-12">
                        <label for="description">题干:</label>
                        <textarea class="form-control" type="text" v-model="name"></textarea>
                    </div>
                    <br>
                    <div class="col-lg-6">
                        <label for="title">选项A</label>
                        <input class="form-control"  type="text" v-model="A">
                    </div>
                    <div class="col-lg-6">
                        <label for="author">选项B</label>
                        <input class="form-control" type="text" v-model="B">
                    </div>
                    <div class="col-lg-6">
                        <label for="author">选项C</label>
                        <input class="form-control" type="text" v-model="C">
                    </div>
                    <div class="col-lg-6">
                        <label for="author">选项D</label>
                        <input class="form-control" type="text" v-model="D">
                    </div>
                    <div class="col-lg-6">
                        <label for="author">正确答案</label>
                        <select class="form-control" v-model="correct">
                            <option disabled value="">请选择</option>
                            <option>A</option>
                            <option>B</option>
                            <option>C</option>
                            <option>D</option>
                        </select>
                    </div>
                    <br>
                    <div class="col-lg-4">
                        
                    </div>
                    <div class="col-lg-4">
                        <br>
                        <button class="btn btn-primary btn-block" @click="submit()">上传</button>                        
                    </div>
                    <div class="col-lg-4">
                    </div>
                </div>
            </div>
            <div class="col-lg-2">

            </div>
        </div>
        

    </div>
</template>

<script>
import api from '../axios'
export default {
    data() {
        return {
            name: '',
            A: '',
            B: '',
            C: '',
            D: '',
            correct: ''
        }
    },
    methods: {
        submit() {
            if(!(this.name && this.A && this.B && this.C && this.D && this.correct)) {
                this.$toast.warn({
                        title:'Upload Problem',
                        message: 'Input all the info',
                        closeButton: false
                    });
                return;
            }
            const data = new FormData();
            data.append('name', this.name);
            data.append('A', this.A);
            data.append('B', this.B);
            data.append('C', this.C);
            data.append('D', this.D);
            data.append('correct', this.correct);
            
            api.upload_problem(data).then((res) => {
                this.$toast.success({
                        title:'Upload Problem',
                        message:'Upload problem successfully~',
                        closeButton: false
                    });
            }).catch((error) => {
                this.$toast.warn({
                        title:'Upload Problem',
                        message: 'Unexpected problem occurred>_<',
                        closeButton: false
                    });
            })
        }
    }
}
</script>


<style>

</style>



