<template>
<div class="test">
    <table class="table table-hover">
        <thead class="thead-inverse">
            <tr>
            <th>ZJUid</th>
            <th>正答数</th>
            <th>答题总数</th>
            <th>正答率</th>
            <th>答题时间</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="user in users">
                <!-- <listvideo :video="video"></listvideo> -->
                <th scope="row">{{user.ZJUid}}</th>
                <td>{{user.correctcnt}}</td>
                <td>{{user.allcnt}}</td>
                <td>{{toPercent(user.correctcnt/user.allcnt)}}</td>
                <td>{{user.updatedAt}}</td>
                
            </tr>
        </tbody>
        </table>
</div>

</template>

<script>
import api from '../axios'
export default {
    data() {
        return {
            users: []
        };
    },
    created() {
        this.init();
    },
    methods: {
        init() {
            api.show_users().then((res) => {
                this.users = res.data;
            });
        },
        toPercent(point){
            var str=Number(point*100).toFixed(1);
            str+="%";
            return str;
        }
    }
    
}
</script>


<style>

</style>



