<template>
<div class="menus">
    <!-- <router-link :to="{name: item.name}" v-for="item in menulists" :key="item.id">
        {{item.title}}
    </router-link> -->
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
        
        <a @click="goTarget(menulists[0].name)" class="navbar-brand" href="javascript:void(0);">答题系统后台</a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto">
                <li v-for="item in menulists" :key="item.id" :class="{'active': item.name === selected}">
                    <a class="nav-link" href="javascript:void(0);" @click="goTarget(item.name)">{{item.title}}<span class="sr-only" v-if="item.name === selected">(current)</span></a></a>
                </li>
            </ul>
            <form class="form-inline my-2 my-lg-0">
            <input class="form-control mr-sm-2" type="search" placeholder="在此键入..." aria-label="Search">
            <button class="btn btn-outline-info my-2 my-sm-0" type="submit">搜索</button>
            </form>
        </div>
    </nav>
</div>
</template>

<script>
export default {
    data() {
        return {
            menulists: [
                {
                    id: 0,
                    title: "仪表盘",
                    name: "main"
                },
                {
                    id: 1,
                    title: "题目上传",
                    name: "upload"
                },
                {
                    id: 2,
                    title: "题目管理",
                    name: "list"
                },
                {
                    id: 3,
                    title: "用户管理",
                    name: "user"
                },
                {
                    id: 4,
                    title: "关于",
                    name: "about"
                }
            ],
            selected: this.$route.name
        };
    },
    methods: {
        goTarget(name) {
            this.selected = name;
        }
    },
    watch: {
        'selected'(id) {
            this.$router.push({'name': id})
        }
    }
};
</script>

<style>

</style>



